import React from "react";
import styled from "styled-components";
import Container from '../common/Container';


const StyledFilter = styled.div`
  /* background-color:purple; */
  text-align: end;
  padding: 2rem 0;

  select {
    border: 2px solid #cccccc;
    padding: 0.5rem 1rem;
    margin-right:-1rem;
    color: #939393;
    font-size: 1.4rem;
    outline: none;
    cursor: pointer;
  }
  select:focus{
    border-color:#cfb53b;
  }
`;

const Filter = ({ change }) => {

  return (
    <Container>
      <StyledFilter>
        <select onChange={change} >
          <option value="popularity.desc">Popularno: od najwyszej</option>
          <option value="popularity.asc">Popularno: od najniszej</option>
          <option value="vote_average.desc">rednia ocen: od najwyszej</option>
          <option value="vote_average.asc">rednia ocen: od najniszej</option>
          <option value="vote_count.asc">Ilo ocen: od najniszej</option>
          <option value="vote_count.desc" >Ilo ocen: od najwyszej</option>
        </select>
      </StyledFilter>
    </Container>
  );
};

export default Filter;
